<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div&gt;\n        &lt;div&gt;\n          &lt;v-badge :count=\&quot;counter\&quot;&gt;\n            &lt;a href=\&quot;#\&quot; class=\&quot;head-example\&quot;&gt;\n          &lt;/a&gt;&lt;/v-badge&gt;\n          &lt;v-button-group&gt;\n            &lt;v-button @click=\&quot;decline\&quot;&gt;\n              &lt;v-icon type=\&quot;minus\&quot;&gt;\n            &lt;/v-icon&gt;&lt;/v-button&gt;\n            &lt;v-button @click=\&quot;increase\&quot;&gt;\n              &lt;v-icon type=\&quot;plus\&quot;&gt;\n            &lt;/v-icon&gt;&lt;/v-button&gt;\n          &lt;/v-button-group&gt;\n        &lt;/div&gt;\n        &lt;div style=\&quot;margin-top: 10px\&quot;&gt;\n          &lt;v-badge :dot=\&quot;show\&quot;&gt;\n            &lt;a href=\&quot;#\&quot; class=\&quot;head-example\&quot;&gt;\n          &lt;/a&gt;&lt;/v-badge&gt;\n          &lt;v-switch v-model=\&quot;show\&quot;&gt;\n        &lt;/v-switch&gt;&lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data:()=&gt; ({\n            counter: 5,\n            show:true\n        }),\n        methods: {\n            decline() {\n                this.counter--;\n            },\n            increase() {\n                this.counter++;\n            }\n        }\n    }\n&quot;,&quot;style&quot;:&quot;\n    .ant-badge:not(.ant-badge-status) {\n        margin-right: 16px;\n    }\n    .head-example {\n        width: 42px;\n        height: 42px;\n        border-radius: 6px;\n        background: #eee;\n        display: inline-block;\n    }\n&quot;}">
                  <div class="box-demo-show" slot="component"><template>
    <div>
        <div>
          <v-badge :count="counter">
            <a href="#" class="head-example">
          </a></v-badge>
          <v-button-group>
            <v-button @click="decline">
              <v-icon type="minus">
            </v-icon></v-button>
            <v-button @click="increase">
              <v-icon type="plus">
            </v-icon></v-button>
          </v-button-group>
        </div>
        <div style="margin-top: 10px">
          <v-badge :dot="show">
            <a href="#" class="head-example">
          </a></v-badge>
          <v-switch v-model="show">
        </v-switch></div>
    </div>
</template>


</div>
