<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="badge-props"><a class="header-anchor" href="#badge-props" aria-hidden="true">¶</a> Badge Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>count</td>
<td>展示的数字，大于 overflowCount 时显示为 ${overflowCount}+，为 0 时隐藏</td>
<td>number</td>
<td>0</td>
</tr>
<tr>
<td>overflowCount</td>
<td>展示封顶的数字值</td>
<td>number</td>
<td>99</td>
</tr>
<tr>
<td>dot</td>
<td>不展示数字，只有一个小红点</td>
<td>boolean</td>
<td>false</td>
</tr>
<tr>
<td>countStyle</td>
<td>设置count的样式</td>
<td>object</td>
<td>-</td>
</tr>
<tr>
<td>status</td>
<td>设置 badge 为状态点('success'、'processing'、'default'、'error'、'warning')</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>text</td>
<td>在设置了 status 的前提下有效，设置状态点的文本</td>
<td>string</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
    .ant-badge:not(.ant-badge-status) {
        margin-right: 16px;
    }
    .head-example {
        width: 42px;
        height: 42px;
        border-radius: 6px;
        background: #eee;
        display: inline-block;
    }
</style>
<script>
    export default {
        data:()=> ({
            counter: 5,
            //overflowCount:99,
            show:true
        }),
        methods: {
            decline() {
                this.counter--;
            },
            increase() {
                this.counter++;
            }
        }
    }
</script>


